<template>
  <div>
    <mt-header title="设置">
      <router-link to="/mine" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="info_list">
      <div>
        <div @click="userSecurity">
          <span>账号与安全</span>
          <span>
              <span class="user_info">账号安全等级</span>
              <img :src="direction">
          </span>
        </div>
        <div @click="help"><span>帮助与反馈</span><span><span class="user_info"></span><img :src="direction"></span>
        </div>
        <div @click="aboutUs"><span>关于我们</span><span><span class="user_info"></span><img :src="direction"></span>
        </div>
      </div>
    </div>
    <div class="exitLogin" @click="exitLogin" v-if="isShowExitBtn">退出登录</div>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  export default {
    components: {
      headerNavBack,
    },
    data() {
      return {
        isShowExitBtn: true,
        direction: require('../../assets/img/je3skwbl.png'),
      }
    },
    mounted() {
      if (!localStorage.getItem('dapeng')) {
        this.isShowExitBtn = false
      }
    },
    methods: {
      back() {
        this.$router.push({
          path: '/mine'
        });
      },
      aboutUs() {
        this.$router.push({
          path: '/aboutUs'
        });
      },
      help() {
        this.$router.push({
          path: '/help'
        });
      },
      exitLogin() {
        try {
          localStorage.removeItem('dapeng')
          this.$Toast({
            message: '退出成功',
            position: 'middle',
            duration: 1000
          })
          this.$store.commit('cleanAllStatus')
          this.$store.state.userModule.LOGIN_USER_OBJECT = null
          setTimeout(() => {
            this.$router.push('/')
          }, 1000)
        } catch (error) {
          // 操作有误
          this.$Toast({
            message: '操作异常',
            position: 'middle',
            duration: 1000
          })
        }
      },
      userSecurity() {
        if (this.$store.state.userModule.LOGIN_USER_OBJECT) {
          this.$router.push({
            name: 'userSecurity'
          });
        } else {
          this.$Toast({
            message: '请登录后操作',
            position: 'middle',
            duration: 1000
          })
        }
      }
    }
  }
</script>
<style lang="less" scoped>
  // 个人信息列表
  .info_list {
    &>div {
      margin-top: 10px;
      &>div {
        display: flex;
        height: 100px;
        background: #fff;
        span {
          display: inline-block;
          flex: 3;
          text-indent: 1em;
          vertical-align: middle;
          height: 100px;
          line-height: 100px;
          color: #777;
          font-size: 30px;
          img {
            vertical-align: middle;
            margin-left: 30px;
            width: 20px;
          }
        }
        span:nth-child(2) {
          text-align: right;
          padding-right: 20px;
          flex: 7;
          .user_info {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
        }
      }
    }
  } // 个人信息列表
  .exitLogin {
    position: fixed;
    bottom: 0;
    left: 50%;
    right: 0;
    height: 100px;
    font-size: 40px;
    color: #44b336;
    background-color: #fff;
    line-height: 100px;
    text-align: center;
    transform: translateX(-50%);
    width: 1020px;
  }
</style>




